import React from 'react';
import Card from "../components/card";
import { useNavigate } from "react-router-dom";
import "../css/empdetail.css";

const Empdetail = () => {
  const navigate = useNavigate();
  let emp =
  {
    "Name": "Aurora",
    "Age": 53,
    "Email": "ante.blandit@disparturient.ca",
    "Date of joining": "10/06/2019",
    "Phone number": "1628101902299",
    "Street": "493 Iaculis Rd.",
    "City": "Lewiston",
    "Zip": "42591-180",
    "Region": "Maine",
    "Country": "Sudan",
    "Info": "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    "EmployeeID": 101
  };
  if (localStorage.getItem('emp'))
    emp = JSON.parse(localStorage.getItem('emp'))

  const back = () => {
    navigate("/emplist");
  }

  return (

    <div id="page">
      <div class="card">

        <Card btnname="back" emp={emp} btnfn={() => back()}></Card>
        <table>
          <tbody id="karan">
            <tr>
              <th>Name :</th>
              <td>{emp.Name}</td>
            </tr>
            <tr>
              <th>Age: </th>
              <td>{emp.Age}</td>
            </tr>
            <tr>
              <th>Email: </th>
              <td>{emp.Email}</td>
            </tr>
            <tr>
              <th>Date of Joining: </th>
              <td>{emp["Date of joining"]}</td>
            </tr>
            <tr>
              <th>Phone No: </th>
              <td>{emp["Phone number"]}</td>
            </tr>
            <tr>
              <th>Street: </th>
              <td>{emp.Street}</td>
            </tr>
            <tr>
              <th>City: </th>
              <td>{emp.City}</td>
            </tr>
            <tr>
              <th>Zip: </th>
              <td>{emp.Zip}</td>
            </tr>
            <tr>
              <th>Region: </th>
              <td>{emp.Region}</td>
            </tr>
            <tr>
              <th>Country: </th>
              <td>{emp.Country}</td>
            </tr>
            <tr>
              <th>Info: </th>
              <td>{emp.Info}</td>
            </tr>
            <tr>
              <th>EmployeeID: </th>
              <td>{emp.EmployeeID}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};
export default Empdetail;