import React from "react";
import profile from "../assets/images.png";

const Card =(props) => {
    return(
      <div className="box">
        <img src={profile} alt="images" width="220" height="250" />
        <div className="table">
          <p>Username :<span>{props.emp.Name}</span></p>
          <p>Id :<span>{props.emp.EmployeeID}</span></p>
        </div>
        <button onClick={()=> props.btnfn()} id="view-btn">{props.btnname}</button>
      </div>
    )
}
export default Card;