import React from "react";
import "../css/login.css";
import { useForm } from "react-hook-form";
import Swal from 'sweetalert2';


const Login = () => {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const onSubmit = (data) => {
        console.log(data);
        Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: 'Your work has been saved',
            showConfirmButton: false,
            timer: 1500
          })
    }
    return (
        <div id="loginForm">
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="hii">
                    <label className="required"><i className7="fa-regular fa-user"></i> Username</label>
                    <input type="text" maxLength="15"{...register('username', {
                        required: {
                            value: true,
                            message: 'username is required'
                        },
                        minLength: {
                            value: 3,
                            message: 'username should have atleast three characters'
                        },
                        maxLength: {
                            value: 10,
                            message: 'username should have atleast ten characters'
                        }
                    })} />
                </div>
                <small>{errors.username?.message}</small>

                <div className="hii">
                    <label className="required"><i className="fa-solid fa-key"></i> Password</label>
                    <input type="password" {...register("password", {
                        pattern: {
                            value: /^(?=.[a-z])[A-Za-z\d@$!%?&]{8,}$/,
                            message: "Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
                        }
                    })} />
                </div>
                <small>{errors.password?.message}</small>

                <div className="hii">
                    <button><i className="fa-solid fa-right-to-bracket"></i> Login</button>
                </div>
            </form>
        </div>
    )
}

export default Login;