import React from 'react';
import "../css/signup.css";
import {useForm} from "react-hook-form";

const Signup = () => {
    const {register,handleSubmit,formState:{errors}} = useForm();
    const onSubmit = (data) => {
        console.log (data);
    }
    return (
        <div id="signupForm">
            <form onSubmit = {handleSubmit(onSubmit)}>
            <div className="hii">
                <label class="required"><i class="fa-solid fa-user"></i> Username</label>
                <input type="text" maxlength="15"{...register("username",{
                    required:{
                        value:true,
                        message:'username is required'
                    },
                    minLength: {
                        value:3,
                        message:'username should have atleast three characters'
                    },
                    maxLength: {
                        value:10,
                        message:'username should have atleast ten characters'
                    }
                })} />
                <small>{errors.username?.message}</small>
            </div>
            <div className="hii">
                <label class="required"><i class="fa-solid fa-lock"></i> Password</label>
                <input type="password" maxlength="10" {...register("password",{
                       pattern:{
                        value:/^(?=.[a-z])(?=.[A-Z])(?=.\d)(?=.[@$!%?&])[A-Za-z\d@$!%?&]{8,}$/,
                        message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
                    }})}/>
            </div>
            <small>{errors.password?.message}</small>

            <div className="hii">
                <label class="required"><i class="fa-solid fa-lock"></i> Re-Type Password</label>
                <input type="password" maxlength="10"{...register("Re-type password",{
                     pattern:{
                        value:/^(?=.[a-z])(?=.[A-Z])(?=.\d)(?=.[@$!%?&])[A-Za-z\d@$!%?&]{8,}$/,
                        message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"}
                })} />
            </div>
            <small>{errors.password?.message}</small>

            <div className="hii">
                <button><i class="fa-solid fa-right-to-bracket"></i>Signup</button>
            </div>
            </form>
        </div>
    )
}

export default Signup;